:root {
    --radius: 10px;
    /* Color Theme Swatches in Hex */
    --My-Color-Theme-1-hex: #59ab99;
    --My-Color-Theme-2-hex: #74f7db;
    --My-Color-Theme-3-hex: #f7b768;
    --My-Color-Theme-4-hex: #3748ab;
    --My-Color-Theme-5-hex: #5c73f7;

    /* Color Theme Swatches in RGBA */
    --My-Color-Theme-1-rgba: rgba(89, 171, 153, 1);
    --My-Color-Theme-2-rgba: rgba(116, 247, 219, 1);
    --My-Color-Theme-3-rgba: rgba(247, 183, 104, 1);
    --My-Color-Theme-4-rgba: rgba(55, 72, 171, 1);
    --My-Color-Theme-5-rgba: rgba(92, 115, 247, 1);

    /* Color Theme Swatches in HSLA */
    --My-Color-Theme-1-hsla: hsla(166, 32, 50, 1);
    --My-Color-Theme-2-hsla: hsla(167, 89, 71, 1);
    --My-Color-Theme-3-hsla: hsla(33, 89, 68, 1);
    --My-Color-Theme-4-hsla: hsla(231, 51, 44, 1);
    --My-Color-Theme-5-hsla: hsla(231, 90, 66, 1);
}
* {
    margin: 0px;
    font-family: "Courier New", Courier, monospace;
}
.holderWindow {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    margin: 0px;
    display: flex;
    align-items: center;
}
.con {
    position: relative;
    max-width: 40em;
    margin: 1em auto;
    z-index: 5;
}
.conHide {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(128, 128, 128, 0.897);
    z-index: 4;
}
.con .title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5em;
}
.con .title *,
.con .button {
    background-color: var(--My-Color-Theme-5-rgba);
}
.con .title * {
    border-top-left-radius: var(--radius);

    border-bottom-left-radius: var(--radius);
    /* border-radius: var(--radius); */
    border: solid blue 2px;
    font-weight: 700;
    font-size: 1.5em;
    padding: 0.1em;
    flex-basis: 100;
    flex-grow: 1;
    text-align: center;
}
.con .title .exit {
    padding: 0px 5px;
    padding-top: 2px;
    border-radius: 0px;
    border-top-right-radius: var(--radius);

    border-bottom-right-radius: var(--radius);
    border: none;
    background-color: rgb(206, 0, 0);
    flex-grow: 0;
    flex-basis: 0;
    cursor: pointer;
}
.con .title .exit:hover {
    background-color: red;
}
.con .window {
    background-color: var(--My-Color-Theme-2-rgba);
    min-height: 10em;
    padding: 1em;
    display: flex;
    gap: 1em;
    flex-wrap: wrap;
    justify-content: space-evenly;
    border-radius: var(--radius);
    align-items: stretch;
}
.con .window form,
.con .window .status,
.con .window .note {
    padding: 0.5em;
    border: black 1px solid;
    border-radius: var(--radius);
    width: fit-content;
}
.con .window form .input,
.input {
    padding: 10px;
    display: flex;
    align-items: center;
}
.input input {
    padding: 5px;
}
.con .window form img,
.con .button img,
img {
    height: 1.5em;
    padding: 5px;
    /* padding-right: 10px; */
}
.con .window .status {
    border: none;
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    cursor: pointer;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: var(--My-Color-Theme-5-hex);
}
.con .window .status * {
    pointer-events: none;
}
.con .window .status:hover {
    background-color: var(--My-Color-Theme-4-hex);
}
.con .window .status * {
    font-size: 1.1em;
    font-weight: bold;
}
.con .window .status .stars {
    /* background-color: gold; */
    position: relative;
    text-shadow: 0px 0px 10px white;
    padding: 0em;
}
.con .window .status .stars span {
    font-size: 2.5em;
    margin: 0em;
    position: relative;
    top: 0.4;
}
.con .window .status .stars span:last-child {
    bottom: 0.16em;
    font-size: 2em;
}
.con .window .status .stars .gold {
    color: gold;
}

.con .window .status .stars .gray {
    color: black;
}
.con .window .status .input input {
    font-weight: normal;
    font-size: 0.9em;
    padding: 0px;
}
.con .window .status div.followers {
    flex-basis: 40px;
}
.con .window .status div.followers span {
    background-color: rgba(153, 205, 50, 0.67);
    padding: 7px;
    margin: 10px;
    border-radius: 0.5em;
}
.con .window .note {
    width: max-content;
    height: max-content;
}
.con .button {
    margin-top: 0.5em;
    border-radius: var(--radius);
    display: flex;
    justify-content: space-around;
    padding: 0.5em;
}
.con .button button {
    display: flex;
    align-items: center;
    padding: 0px 1em;
    background-color: var(--My-Color-Theme-4-rgba);
    color: white;
    font-weight: 700;
    font-size: 1.3em;
}
.con .button button span {
    padding-top: 3px;
}
.con .button button:hover {
    background-color: rgb(37, 37, 37);
    cursor: pointer;
}
